.aboutus-section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
}

.aboutus-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  text-align: center;
}

.aboutus-image img{
  width: 100%;
}

.aboutus-image {
  width: 50%;
}

.aboutus-text p{
  width: 80%;
}

@media screen and (max-width: 1300px) {
  .aboutus-section {
    flex-direction: column;
    align-items: center;
    justify-content: start;
  }
  .aboutus-text {
    width: 80%;
  }

  .aboutus-image {
    width: 80%;
  }
  .aboutus-text p{
    width: 100%;
    font-size: 1.8em;
  }

  .aboutus-text h1{
    font-size: 2.5em;
  }

}

@media screen and (max-width: 768px) {
  .aboutus-section {
    flex-direction: column;
    align-items: start;
  }
  .aboutus-text {
    width: 100%;
  }

  .aboutus-image {
    width: 100%;
  }

  .aboutus-image img{
    width: 100%;
  }
  
}