.product-container {
    z-index: -1;
    display: flex;
    width: 75%;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;
    justify-content: space-between;
}


.product-container h2 {
    font-size: 2rem;
    margin: 0;
    padding: 0;
    margin-top: 20px;
    margin-bottom: 20px;
    color: #333;
    font-weight: 400;
    text-align: center;
    width: 100%;
    border-bottom: 1px solid #333;
    padding-bottom: 10px;
}

.product-container p {
    font-size: 1.2rem;
    margin: 0;
    padding: 0;
    margin-top: 20px;
    margin-bottom: 20px;
    color: #333;
    font-weight: 400;
    text-align: center;
    width: 100%;
    padding-bottom: 10px;
}

.product-container img {
    width: 100%;
    height: auto;
    margin-top: 20px;
    margin-bottom: 20px;
}



#display-container {
    z-index: -1;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 20px;
}