/* ∞∞ global ∞∞ */
.global * {
	position: relative;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	border-color: inherit;
}



/* ∞∞ text styling ∞∞ */
h1, h2, h3, h4, h5, h6, p, em, a, b, strong {
	font-style: normal;
	font-weight: 300;
}

strong {
	font-size: 1.15em;
}

small {
  font-size: .85em;
}

ul,
ol,
menu {
	list-style: none;
}

sub,
sup {
  font-size: .75em;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

abbr[title] {
  border-bottom: 1px dotted;
}

dfn {
  font-style: italic;
}

/* ∞∞ form elements ∞∞ */
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
  background: transparent;
	outline: none;
	border: none;
	border-radius: 0;
	-webkit-appearance: none;
	   -moz-appearance: none;
			appearance: none;
}

button,
select {
  text-transform: none;
  cursor: pointer;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

fieldset {
  border: none;
  margin: 0;
  padding: 0;
	min-width: 0;
}

textarea {
	resize: none;
}

/* ∞∞ link styling ∞∞ */
a {
	display: inline;
	text-decoration: none;
	color: inherit;
	cursor: pointer;
}

a:active,
a:hover {
  outline: 0;
}

