/* ∞∞ hr using box ∞∞ */
hr {
	border: none;
  box-sizing: border-box;
  height: .075rem;
}

/* ∞∞ hidden ∞∞ */
[hidden],
template {
  display: none;
}

/* ∞∞ media specific ∞∞ */
audio:not([controls]) {
  display: none;
  height: 0;
}

/* ∞∞ tables ∞∞ */
table {
  border-collapse: collapse;
  border-spacing: 0;
  border: none;
}

td,
th {
	margin: 0;
  padding: 0;
  font-weight: inherit;
	text-align: inherit;
}


/*====================================================== ∞∞ browser specific ∞∞ */

/* ∞∞ IE ∞∞ */
article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

a {
  background-color: transparent;
}

button {
  overflow: visible;
}

html {
  -ms-text-size-adjust: 100%;
}

img {
  border: none;
}

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

legend {
  border: none;
  padding: 0;
}

mark {
  background: #ff0;
  color: #000;
}

svg:not(:root) {
  overflow: hidden;
}

textarea {
  overflow: auto;
}

/* ∞∞ moz ∞∞ */
body {
	-moz-osx-font-smoothing: grayscale;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: none;
  padding: 0;
}

input {
  line-height: normal;
}

/* ∞∞ webkit ∞∞ */
* {
	-webkit-margin-before: 0;
	-webkit-margin-after: 0;
}

html {
  -webkit-text-size-adjust: 100%;
}

body {
	-webkit-font-smoothing: antialiased !important;
}

blockquote {
	-webkit-margin-before: 0;
	-webkit-margin-after: 0;
	-webkit-margin-start: 0;
	-webkit-margin-end: 0;
}

fieldset {
	-webkit-margin-start: 0;
	-webkit-margin-end: 0;
	-webkit-padding-before: 0;
	-webkit-padding-start: 0;
	-webkit-padding-end: 0;
	-webkit-padding-after: 0;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
  height: auto;
}

input[type="search"] {
  -webkit-appearance: textfield;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration,
progress {
  -webkit-appearance: none;
}


form {
	z-index: 0;
width: 100%;
height: 100%;
margin-top: 20px;
padding-bottom: 75px;
display: flex;
justify-content: center;
align-items: center;
background-image: white;
}

.select {
	position: relative;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	border-color: inherit;
width: 225px;
height: 40px;
cursor: pointer;
background-color: white;
box-shadow: 0 2px 0 white;
border-radius: 2px;
}

.select_expand {
	width: 0;
	height: 40px;
	position: absolute;
	top: 0;
	right: 0;
}

.select_expand::after {
		content: '\003E';
		position: absolute;
		top: 50%;
		right: 0;
		transform: translate(-50%, -50%) rotate(90deg) scaleY(1.75);
		color: #3e3e3e;
		font-size: 28px;
		pointer-events: none;
		z-index: 2;
		transition: all 250ms cubic-bezier(.4,.25,.3,1);
		opacity: .6;
	}

.select_expand:hover::after {opacity: 1}

.select_expand:checked::after {transform: translate(-50%, -50%) rotate(90deg) scaleX(-1) scaleY(1.75);}

.select_expandLabel {
	display: block;
	width: 100%;
	height: 40px;
	position: absolute;
	top: 0;
	left: 0;
	cursor: pointer;
}

.select_close {display: none}

.select_closeLabel {
	width: 100vw;
	height: 100vh;
	position: fixed;
	top: 0;
	left: 0;
	display: none;
}

.select_items {
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	border: 2px solid #2fb5d1;
	border-radius: 2px;
	padding-top: 40px;
}

.select_input {display: none}

.select_label {
	transition: all 250ms cubic-bezier(.4,.25,.3,1);
	display: block;
	height: 0;
	font-size: 1.2rem;
	line-height: 40px;
	overflow: hidden;
	color: #3e3e3e;
	background-color: #fff;
	cursor: pointer;
	padding-left: 20px;
}

.select_label-placeholder {
		height: 40px;
		vertical-align: middle;
		position: absolute;
		top: 0;
		left: 0;
		opacity: .6;
		background-color: transparent;
	}

.select_expand:checked + .select_closeLabel {
		display: block;
	}

.select_expand:checked + .select_closeLabel + .select_options .select_label {
				height: 40px;
			}

.select_expand:checked + .select_closeLabel + .select_options .select_label:hover {background-color: #f7f7f7}

.select_expand:checked + .select_closeLabel + .select_options + .select_expandLabel {display: none}

.select_input:checked + .select_label {
	height: 40px;
	margin-top: -40px;
}